	module.exports = {"data":{"wordpressWpLessons":{"id":"e8c072e0-436e-5ff3-85ab-d6e6184215e9","wordpress_id":993,"title":"Part Three: Capstone Project Reflection","slug":"part-three-capstone-project-reflection","content":"<p>Congratulations! You&#8217;re in the last stages of the project now. After you&#8217;re finished up with the project you&#8217;ll need to complete a Project Reflection portfolio entry. At the Showcase, you&#8217;ll have your portfolio on a computer next to you, so be prepared to show your entry to attendees. For this reason, you&#8217;ll want to make sure to plan enough time to make a polished reflection entry that includes photos and well-organized text, and even a video if it&#8217;s appropriate for your project. </p>\n<h1>Project Reflection</h1>\n<p>In a <em><strong>Project Reflection</strong></em> post on your portfolio website, you will be writing about how your project turned out. This is written to reflect on your project, your new learning, new skills you gained, and anything else about the process you would like to share. A Project Reflection post is written <strong>when a project is completed </strong>(or when a version of a project is completed).</p>\n<p>When you write a Project Reflection, please answer <span style=\"color: #c73030;\"><strong>ALL</strong></span> of the following questions in a few paragraphs:</p>\n<ul>\n<li>What was the project prompt?</li>\n<li>What is your project</li>\n<li>Why did you make this project?</li>\n<li>How did you make it? What was your process? What steps did you follow?</li>\n<li>What problems or challenges did you face? How did you overcome these?</li>\n<li>What new skills or concepts did you learn through this project?</li>\n<li>What would you do differently next time?</li>\n<li>What would you tell someone else who was going to make this project?</li>\n</ul>\n"}},"pathContext":{}}

